import React, { Component } from 'react';

class Card extends Component {

  render(){
    return(
      <div className="card">
        <h5 className="card-header">
          { this.props.headerText }
        </h5>
        <div className="card-body">
          { this.props.bodyContent }
        </div>
      </div>
    )
  }

}
export default Card;