import React, { Component } from 'react';

class Form extends Component {

  constructor( props ){
    super(props);
    this.initialState = {
      nombre: '',
      cargo: ''
    }
    this.state = this.initialState;
  }

  cambiar = (event) => {
    const { name, value } = event.target;
    console.log(event.target.name);
    this.setState({
      [name] : value, //En 1 sola linea asigna el valor (value del input) al respectivo atributo del estado (name del input)
    });
  }

  enviarForm = () => {
    this.props.enviar(this.state);
    this.setState(this.initialState);
  }

  render() {
    const { nombre, cargo } = this.state;
    return(
      <form>
      <div className="form-group">
        <label htmlFor='nombre'>Tu nombre:</label>
        <input type='text' name='nombre' id='nombre' value={nombre} onChange={this.cambiar} placeholder='John Doe' className='form-control'/>
      </div>
        <div className="form-group">
          <label htmlFor='cargo'>Tu cargo:</label>
          <input type='text' name='cargo' id='cargo' value={cargo} onChange={this.cambiar} placeholder='Desarrollador web' className='form-control'/>
        </div>
        <input type='button' value='Pasalos a la tabla' onClick={this.enviarForm} className='btn btn-primary btn-lg btn-block'/>
      </form>
    );
  }

}
export default Form;