import React, { Component } from 'react';

//Function ES6
const TableHeader = () => {
  return(
    <thead className="text-center">
      <tr>
        <th>Nombre</th>
        <th>Cargo</th>
        <th>Acci√≥n</th>
      </tr>
    </thead>
  )
}
//Function normal
function TableBody ( props ) {
  const rows = props.datos.map((row,index) => {
    return(
      <tr key={index} className="text-center">
        <td>{ row.nombre }</td>
        <td>{ row.cargo }</td>
        <td>
          <button onClick={() => props.removerPersona(index)} className="btn btn-danger">Eliminar</button>
        </td>
      </tr>
    )
  })
  return(
    <tbody>{ rows }</tbody>
  )
}

class Table extends Component {

  render() {

    const {datos,removerPersona} = this.props;
    return(
      <table className="table table-striped">
        <TableHeader />
        <TableBody datos={datos} removerPersona={removerPersona}/>
      </table>
    )
  }

}

export default Table