import React, { Component } from 'react';

import Table from './Components/Table';
import Form from './Components/Form';
import Navbar from './Components/Navbar';
import Card from './Components/Card';
// import { datos } from './Data/data';

class App extends Component {
    
    constructor(props){
      super(props);
      this.state = {
        datos: []
      };
    }

    removerPersona = (index) => {
      const {datos} = this.state;

      this.setState({
        datos: datos.filter((dato,i) => {
          return i !== index;
        })
      });
    }

    enviar = (dato) => {
      this.setState({
        datos: [...this.state.datos, dato]
      });
    }
    
    render() {
      // const {datos} = this.state;
      let tabla, form;
      form = <Form enviar={ this.enviar }/>;

      if ( this.state.datos.length > 0 ){
        tabla = <Table datos={ this.state.datos } removerPersona={this.removerPersona}/>;
      }

      return (
        <div>
          <Navbar />
          <div className="container my-5">
            <div className='row'>
              <div className='col-md-6 col-sm-12 col-xs-12 my-2'>
                <Card headerText={'Ingresa estos datos'} bodyContent={form}/>
              </div>
              <div className='col-md-6 col-sm-12 col-xs-12 my-2'>
                <Card headerText={'Tabla de datos'} bodyContent={tabla}/>
              </div>
            </div>
          </div>
        </div>
      )
  }
}

export default App